<template id="myheader">
	<div id="headerWrap">
		<span class="txt">{{txt}}</span>
		<slot name='left'></slot>
		<slot name='right'></slot>
		<slot></slot>
	</div>
</template>
<script type="text/javascript">
	export default{
		props:['txt']
	}
</script>
<style type="text/css">
	#headerWrap{width: 100%;height:1.32rem;line-height:1.32rem;background: #eee;text-align: center;position: relative;font-weight: bold;}
	#headerWrap span{font-size: .5rem;color: #424242}
		#headerWrap img{width:.75rem;position: absolute;top: .3rem;}
		#headerWrap .left{right:2.4rem;}
		#headerWrap .right{right:.6rem;}
		#headerWrap input{width: 80%;height:.9rem;text-align: left;position: absolute;left: .75rem;top:.24rem;border-radius:1.5rem;border: none;background-color: #fff;text-indent: 3em;}
</style>


